@import "../mixin.scss";

.main {
  text-align: center;

  &__title {
    font-size: 64px;
    font-weight: 500;
    line-height: 84.5px;
    margin-bottom: 3px;
  }

  &__subtitle {
    font-size: 14px;
    font-weight: 500;
    line-height: 20.7px;
    margin-bottom: 24px;
  }

  &__form {
    display: flex;
    flex-direction: column;
    max-width: 320px;
    text-align: left;
    margin-right: auto;
    margin-left: auto;

    &-field {
      @include form-field;
      position: relative;
    }

    &-label {
      @include form-field;
    }

    &-text-select {
      @include label-text;
      margin-bottom: 5px;
    }

    &-text-radio {
      @include label-text;
      margin-bottom: 8px;
    }
  }

  &__form-error {
    position: absolute;
    right: 0;
    color: red;
    font-size: 12px;
    line-height: 12px;
    text-align: right;

    &-checkbox {
      right: 50%;
    }
  }

  &__form-button {
    max-width: 130px;
    width: 100%;
    height: 36px;
    padding: 8px 7px;
    box-sizing: border-box;
    border-color: transparent;
    border-radius: 48px;
    font-weight: 900;
    color: #fff;
    background-color: #c50000;
    text-transform: uppercase;

    &:hover {
      cursor: pointer;
    }
  }
}
